/**
 * @file mockË£ÖÈ•∞Âô® Âíå Ê∂àÊÅØË£ÖÈ•∞Âô®ÈÉΩÊòØÂ∑•ÂÖ∑ÂáΩÊï∞, ‰∏éaxios-serviceÊ≤°ÊúâÂÖ≥ËÅî, ÂèØË£ÖÈ•∞‰ªª‰ΩïËøîÂõûPromiseÁöÑÂáΩÊï∞, Ëøô‰∫õË£ÖÈ•∞Âô®Êõ¥Â§öÊèê‰æõÁöÑÂè™ÊòØ‰∏Ä‰∏™Ë£ÖÈ•∞ÁöÑÊÄùË∑Ø, ÂºÄÂèëËÄÖÂèØËá™Áî±Êâ©Â±ïËá™ÂÆö‰πâË£ÖÈ•∞Âô®, Â¶ÇÂºÇÊ≠•ÂèÇÊï∞‰æùËµñ, Âçï‰æã, loadingÁ≠âÁ≠â
 * @author libaoxu
 * TODO: ÈúÄË¶Å‰ΩøÁî®createDecoratorÊù•ÈáçÂÜô
 */
import createDecorator from '@inkefe/create-decorator'
import { deprecatedHoc, compose } from './utils'

/**
 * ÈÄöËøáÁéØÂ¢ÉÂèòÈáèËé∑ÂèñmockË£ÖÈ•∞Âô®
 * @param {String} isDev ÊòØÂê¶‰∏∫ÂºÄÂèëÁéØÂ¢É
 * @return {Function}
 * TODO: Âõ†ÂÅö‰∫ÜxhrÁöÑÊã¶Êà™, ÊâìÂá∫logger, Âπ∂Ê†áËÆ∞Â•ΩÈ¢úËâ≤ÂíåÂàÜÁªÑÈÇ£Áßç
 */
export function getMockDecoratorByEnv (isDev) {
  /**
   * mockË£ÖÈ•∞Âô®
   * @param {Function} mockFn mockÁöÑÂáΩÊï∞ÈÄªËæë
   * @return {Function}
   */
  return function mockDecorator (mockFn) {
    return createDecorator(apiFn => (...args) => {
      // ÂºÄÂèëÁéØÂ¢ÉËµ∞mock, Â¶ÇÊûúÈúÄË¶ÅÂÖ≥Èó≠, ÈúÄË¶ÅÂÜçapisÁßçÂà†Èô§
      if (isDev) {
        return mockFn(...args)
      } else {
        return apiFn(...args)
      }
    })
  }
}

/**
 * mockË£ÖÈ•∞Âô®
 *
 * Ê≥®: Âõ†‰∏∫ÊúÄÊñ∞Áâàaxios-service ‰ºöÊûÑÂª∫Âá∫‰∏§‰∏™ process.env.NODE_ENV ÂèòÈáèÁöÑjs, Âπ∂Âú®ÂÖ•Âè£index.jsÂà§Êñ≠, ÂºÄÂèëÊ®°ÂºèÂíåÊûÑÂª∫‰ºöÂºïÂÖ•‰∏çÂêåÁöÑjs, ËøôÊ†∑ËÉΩ‰øùËØÅdevÊ®°ÂºèÂíåprodÊ®°ÂºèÁöÑÂå∫ÂàÜ
 *
 * @param {Function} mockFn mockÁöÑÂáΩÊï∞ÈÄªËæë
 * @return {Function}
 */
export const mockDecorator = getMockDecoratorByEnv(process.env.NODE_ENV === 'development')

/**
 * Ê∂àÊÅØÊèêÁ§∫Ë£ÖÈ•∞Âô®, ‰æùËµñ‰∫éÂ§ñÂ±ÇtoastÂØπË±°, Êèê‰æõtoast.success Âíå toast.error‰∏§‰∏™ÂáΩÊï∞
 * @param {Object} toast
 * @param {Function} toast.success
 * @param {Function} toast.error
 */
export const getMessageDecorator = toast =>
  /**
   * È¢ÑÂà∂ÁöÑÊ∂àÊÅØÂÜÖÂÆπ
   *
   * @param {Object} { successMsg: String|Function, errorMsg: String|Function }
   * @property {String|Function} successMsg: ÊàêÂäüÁöÑÊ∂àÊÅØ
   * @property {String|Function} errorMsg: Â§±Ë¥•ÁöÑÊ∂àÊÅØ
   */
  ({ successMsg, errorMsg } = {}) => {
    // eslint-disable-next-line no-console
    const alert = typeof window !== 'undefined' ? window.alert : console.log
    const getToast = name => (typeof toast === 'object' && typeof toast[name] === 'function') ? toast[name] : alert
    const messageGetter = msg => (typeof msg === 'function') ? msg : _ => msg
    const successToast = getToast('success')
    const errorToast = getToast('error')
    const getSuccessMsg = messageGetter(successMsg)
    const getErrorMsg = messageGetter(errorMsg)

    return createDecorator(fn => (...args) => {
      return typeof fn === 'function' && fn(...args).then((res) => {
        const msg = getSuccessMsg(res)
        msg && successToast(msg)
        return Promise.resolve(res)
      }, (err) => {
        const msg = getErrorMsg(err)
        msg && errorToast(msg)
        return Promise.reject(err)
      })
    })
  }

/**
 * errorMsg Ê∂àÊÅØÈ´òÈò∂ÂáΩÊï∞, ÈíàÂØπÁªùÂ§ßÂ§öÊï∞ error
 * @param {String} msg È¢ÑÁΩÆÊ∂àÊÅØ
 *
 * @example
 * class Api {
 *  @messageDecorator({ errorMsg: getErrorMsg('ËØ∑Ê±ÇÂ§±Ë¥•ËØ∑ÈáçËØï') })
 *  requestAAA = get('api/requestAAA')
 * }
 *
 * or
 *
 * // Â£∞ÊòéÂ•ΩÂÖ¨ÂÖ±ÂèòÈáè, Â∞Ü 'ËØ∑Ê±ÇÂ§±Ë¥•ËØ∑ÈáçËØï' ‰ø°ÊÅØcurryÂåñËµ∑Êù•, Êñπ‰æøÂ§öÊ¨°Â§çÁî®
 * const requestFailErrMsg = getErrorMsg('ËØ∑Ê±ÇÂ§±Ë¥•ËØ∑ÈáçËØï')
 *
 * class Api {
 *  @messageDecorator({ errorMsg: requestFailErrMsg})
 *  requestAAA = get('api/requestAAA')
 *
 *  @messageDecorator({ errorMsg: requestFailErrMsg})
 *  requestBBB = get('api/requestBBB')
 * }
 */
export const getErrorMsg = (msg = '') => error => error && (error.msg || msg)

/**
 *
 * @param {Function} requestPathWrapper axios-service ÁöÑ getRequestsByRoot ‰∫ßÂá∫ÁöÑrequestPathWrapperÂáΩÊï∞, Â¶Çget, post
 * @param {Object} opts ËØ∑Ê±ÇÈÖçÁΩÆÈ°πÂØπË±°
 * @property {String} opts.msgKey serverÁ´ØËØ∑Ê±Çmsg
 * @property {String} opts.dataKey serverÁ´ØÊï∞ÊçÆÁöÑkey
 * @property {String} opts.codeKey serverÁ´ØËØ∑Ê±ÇÁä∂ÊÄÅÁöÑkey
 * @property {Number} opts.successCode serverÁ´ØËØ∑Ê±ÇÊàêÂäüÁöÑÁä∂ÊÄÅ, Ê≥®ÊÑè: ÊòØÊúçÂä°Á´ØËøîÂõûÁöÑÁä∂ÊÄÅÁ†Å, ‰∏çÊòØxhrÂú®ÊµèËßàÂô®Á´ØÁöÑËøîÂõûÁä∂ÊÄÅ
 *
 * @example
 * const requestOpts = { msgKey: 'Msg', dataKey: 'Code', codeKey: 'Code', successCode: 200 }
 * const get = requestOptsWrapper(baseGet, requestOpts)
 * const post = requestOptsWrapper(basePost, requestOpts)
 *
 * class Apis {
 *  getA = get('/api/getA')
 *  getB = get('/api/getB')
 *  postA = post('/api/postA')
 *  postB = post('/api/postB')
 * }
 */
export const requestOptsWrapper = (requestPathWrapper, opts) => (path, ...args) => requestPathWrapper(path, opts, ...args)

/**
 * Â∞Ü requestPathWrapper ËΩ¨Êç¢‰∏∫ requestÂáΩÊï∞,Âπ∂Â∞ÜÂÖ∂‰ªñÂèÇÊï∞preArgs, ‰∏ÄËµ∑ÈÄè‰º†Âà∞Ë£ÖÈ•∞ÂáΩÊï∞
 * @param {Function} fn Ë£ÖÈ•∞ÂáΩÊï∞
 *
 * @example
 * const customRequest = requestConnector((request, customArgs) => (data, ...args) => request(...))
 * const customGet = customRequest(get)
 */
const requestConnector = fn => (requestPathWrapper, ...preArgs) => (path, ...args) => {
  const request = requestPathWrapper(path, ...args)
  return fn(request, ...preArgs)
}


const requestToSetParams = (request, customParams) => (data, config = {}) => {
  return typeof request === 'function' && request(data, {
    ...config,
    params: {
      ...config.params,
      ...customParams
    },
  })
}

const requestToSetData = (request, customData) => (data, ...args) => request({ ...customData, ...data }, ...args)

const decoratorsReadmeUrl = 'https://github.com/libaoxu/axios-service#%E6%9B%B4%E5%A4%9A%E8%A3%85%E9%A5%B0%E5%99%A8'

/**
 * @name Êª°Ë∂≥ÊèêÂâçÈ¢ÑÁΩÆËá™ÂÆö‰πâparams, ‰∏çÁÆ°get or post ÈÉΩÊâ©Â±ïÂà∞ Query String Parameters‰∏ä
 * @param {Function} fn axios-service ÁöÑ getRequestsByRoot ‰∫ßÂá∫ÁöÑrequestPathWrapperÂáΩÊï∞, Â¶Çget, post
 * @param {Object} customParams
 * @deprecated
 *
 * @example
 * const customParams = { uid: 123, sid: 'abc' }
 * const postWithAtom = setCustomParamsWrapper(post, customParams)
 * const getWithAtom = setCustomParamsWrapper(get, customParams)
 *
 *
 * class Apis {
 *  getA = getWithAtom('/api/getA')
 *  postB = postWithAtom('/api/postB')
 * }
 */
export const setCustomParamsWrapper = compose(
  deprecatedHoc('setCustomParamsWrapper', decoratorsReadmeUrl),
  requestConnector
)(requestToSetParams)

/**
 * dataÊâ©Â±ï: getËØ∑Ê±ÇÊâ©Â±ïÂà∞ Query String Parameters‰∏ä, postËØ∑Ê±ÇÊâ©Â±ïÂà∞ payload‰∏ä
 *
 * @param {Function} fn  axios-service ÁöÑ getRequestsByRoot ‰∫ßÂá∫ÁöÑrequestPathWrapperÂáΩÊï∞, Â¶Çget, post
 * @param {Object} customData È¢ÑÁΩÆËØ∑Ê±ÇÁöÑdata
 * @deprecated
 * @example
 * const customData = { uid: 123, sid: 'abc' }
 * const postWithCustomData = setCustomDataWrapper(post, customData)
 * const getWithCustomData = setCustomDataWrapper(get, customData)
 *
 *
 * class Apis {
 *  getA = getWithCustomData('/api/getA')
 *  postB = postWithCustomData('/api/postB')
 * }
 */
export const setCustomDataWrapper = compose(
  deprecatedHoc('setCustomDataWrapper', decoratorsReadmeUrl),
  requestConnector
)(requestToSetData)

/**
 * Ë£ÖÈ•∞Âô®ÁõÆÁöÑ: ÂèØ‰ª•Â∞ÜÂõ∫ÂÆöÊï∞ÊçÆÊ≥®ÂÖ•Âà∞ËØ∑Ê±ÇÁöÑqueryString‰∏≠
 * @param {Object} customParams ÈúÄË¶ÅÂú®requestÊó∂Ê≥®ÂÖ•Âà∞queryString‰∏≠ÁöÑÊï∞ÊçÆ
 * @example
 * class Apis {
 *   @setParamsDecorator({ uid, sid })
 *   getUserInfo = get('/user/info')
 * }
 */
export const setParamsDecorator = customParams => createDecorator((fn) => requestToSetParams(fn, customParams))

/**
 * Ë£ÖÈ•∞Âô®ÁõÆÁöÑ‰∏∫: ÂèØ‰ª•Â∞ÜÂõ∫ÂÆöÊï∞ÊçÆÊ≥®ÂÖ•ËØ∑Ê±ÇÁöÑÊï∞ÊçÆ‰∏≠, post(Â∞ÜÊï∞ÊçÆÂõ∫ÂÆöÂà∞body‰Ωì‰∏≠), getËØ∑Ê±Ç(Â∞ÜÊï∞ÊçÆÂõ∫ÂÆöÂà∞query string‰∏≠)
 * @param {Object} customData ÈúÄË¶ÅÂú®requestÊó∂Ê≥®ÂÖ•Âà∞body‰Ωì‰∏≠ÁöÑÊï∞ÊçÆ
 * @example
 * class Apis {
 *   @setDataDecorator({ uid, sid })
 *   getUserInfo = get('/user/info')
 * }
 */
export const setDataDecorator = customData => createDecorator((fn) => requestToSetData(fn, customData))

/**
 * Ê†πÊçÆÁéØÂ¢ÉÂèòÈáèËé∑ÂèñÂª∂Êó∂Ë£ÖÈ•∞Âô®, ÂÅö‰∏Ä‰∏™‰øùÊä§Êú∫Âà∂, ‰ª•Èò≤productionÁéØÂ¢É‰πüÂª∂Êó∂, ÂºïËµ∑Á∫ø‰∏äbug
 * @param {Boolean} isDev ÊòØÂê¶‰∏∫ÂºÄÂèëÁéØÂ¢É
 * webÁ´Ø:
 * const delayDecorator = getDelayDecorator(process.env.NODE_ENV === 'development')
 * RNÁ´Ø:
 * const delayDecorator = getDelayDecorator(__DEV__)
 * Â∞èÁ®ãÂ∫èÁ´Ø:
 * const delayDecorator = getDelayDecorator(ÁéØÂ¢ÉÂèòÈáèÂêç === devÁéØÂ¢ÉÂèòÈáèÂÄº)
 */
export const getDelayDecorator = isDev => {
  return (wait) => createDecorator(fn => (...args) => {
    if (isDev) {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve(fn(...args))
        }, wait || 0);
      })
    } else {
      return fn(...args)
    }
  })
}

/**
 * Âª∂Êó∂Ë£ÖÈ•∞Âô®, Â∑≤ÁªèÊ†πÊçÆÁéØÂ¢ÉÂèòÈáèÂå∫ÂàÜ, ÈÄÇÁî®‰∫éwebÁ´ØÈ°πÁõÆ, Â∞èÁ®ãÂ∫èÂíårnÁ´ØÂ∫îËØ•‰ΩøÁî®‰∏äÈù¢`getDelayDecorator`Êù•ÂàõÂª∫Êñ∞ÁöÑÂª∂Êó∂Ë£ÖÈ•∞Âô®
 * class A {
 *  // Âª∂Êó∂3sËØ∑Ê±Ç
 *  @delayDecorator(3000)
 *  getUserInfo = get('/user/info')
 * }
 */
export const delayDecorator = getDelayDecorator(process.env.NODE_ENV === 'development')
